@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sign-in{padding:100px;display:flex;flex-direction:column;justify-content:space-around;height:500px}.logo{width:40px;height:40px}header{display:flex;align-items:center;justify-content:center;flex-direction:column}header p{font-size:25px;margin:5px;margin-bottom:10px}.header-img{display:flex;align-items:center;justify-content:center;width:60px;height:60px;background-color:#c49c9c;border-radius:30px}.sign-in footer{display:flex;justify-content:space-around;width:450px;flex-wrap:wrap}footer p{margin-top:40px}
.sign-up{padding:100px;display:flex;flex-direction:column;justify-content:space-around;height:520px;width:445px}.user-name{display:flex;justify-content:space-between}.user-name #outlined-basic{width:190px}footer{width:450px;display:flex;justify-content:flex-end}.sign-up p{display:flex;justify-content:center}
.App {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 753px;
    font-family: 'Roboto', sans-serif;
    color: rgb(56, 55, 55);
}

